.contact__line {
    color: #FE7A15;;
    padding-right: 12%;
}

.btn {
    font-size: 16px;
}

.buttons {
    display: flex;
    align-items: center;
    justify-content: center;
    max-width: 350px;
    flex-direction: column;
    margin: 0 auto;
    padding-top: 3rem;
}

.telephone, .whatsapp {
    max-height: 20px;
    align-self: baseline;
    margin-left: 5px;
}

.btn {
  appearance: button;
  border: solid transparent;
  border-radius: 16px;
  border-width: 0 0 4px;
  box-sizing: border-box;
  color: #FFFFFF;
  cursor: pointer;
  display: inline-block;
  font-family: din-round,sans-serif;
  font-size: 15px;
  font-weight: 700;
  letter-spacing: .8px;
  line-height: 20px;
  margin: 0;
  outline: none;
  overflow: visible;
  padding: 13px 16px;
  text-align: center;
  text-transform: uppercase;
  touch-action: manipulation;
  transform: translateZ(0);
  transition: filter .2s;
  user-select: none;
  -webkit-user-select: none;
  vertical-align: middle;
  white-space: nowrap;
  width: 100%;
}

.btn:after {
  background-clip: padding-box;
  border: solid transparent;
  border-radius: 16px;
  border-width: 0 0 4px;
  bottom: -4px;
  content: "";
  left: 0;
  position: absolute;
  right: 0;
  top: 0;
  z-index: -1;
}

.btn:main,
.btn:focus {
  user-select: auto;
}

.btn:hover:not(:disabled) {
  filter: brightness(1.1);
  -webkit-filter: brightness(1.1);
}

.btn:disabled {
  cursor: auto;
}

.whatsapp_button {
  background-color: #25D366;
  margin-bottom: 4rem;
}

.whatsapp_button:after {
  background-color: #3cf07e;
}

.phone_button {
  background-color: #1899D6;   
}

.phone_button:after {
  background-color: #1CB0F6;
}

.table .wrapper {
  margin-bottom: 10%;
}

table { 
  border-spacing: 1; 
  border-collapse: collapse; 
  background: #fff9e9;
  border-radius: 6px;
  overflow: hidden;
  width: 100%;
  position: relative;
}
  
td,th { 
  padding-left: 8px;
  text-align: left;
}

td {
  border: 2px solid #ddd;
  line-height: 40px;
}

thead tr { 
  height: 60px;
  background: #FFED86;
  font-size: 16px;
}

@media screen and (max-width: 768px) {
  table {
    font-size: 12px;
  }
}

@media screen and (max-width: 576px) {
  .contact {
    padding-top: 6rem;
  }

  .buttons {
    max-width: 250px;
  }

  .telephone, .whatsapp {
      max-height: 15px;
      margin-left: 3px;
  }

  .btn {
    border-radius: 12px;
    border-width: 0 0 3px;
    font-size: 12px;
    font-weight: 700;
    letter-spacing: .6px;
    line-height: 15px;
    padding: 11px 13px;
  }

  table {
    width: 100%;
  }

  table thead {
    display: none;
  }

  table tr, table td {
    border-bottom: 2px solid #ddd;
  }

  table tr {
    margin-bottom: 8px;
  }

  table td {
    display: flex;
  }
  
  table td::before {
    content: attr(data-label);
    font-weight: bold;
    width: 120px;
    min-width: 120px;
  }
}

@media screen and (max-width: 350px) {
  .buttons {
    max-width: 180px;
  }

  .telephone, .whatsapp {
      max-height: 12px;
      margin-left: 2px;
  }

  .btn {
    border-radius: 9px;
    border-width: 0 0 2px;
    font-size: 10px;
    font-weight: 700;
    letter-spacing: .5px;
    line-height: 12px;
    padding: 10px 12px;
  }
}